@import '../../utils/mixins.scss';

.category {
  display: flex;
  justify-content: center;

  &__title {
    margin-bottom: 24px;
  }

  &__img {
    height: 368px;
    width: 368px;
  }

  &__cards {
    display: flex;
    box-sizing: border-box;
    flex-direction: column;

    @include onTablet {
      flex-direction: row;
      justify-content: space-around;
      flex-wrap: wrap;
    }
  }

  &__card {
    font-size: 14px;
    font-weight: 500;
    line-height: 21px;
    color: rgba(137, 147, 154, 1);
    text-decoration: none;
    display: inline-block;

    transition:
      transform 1s ease,
      color 1s ease;

    &:hover {
      transform: scale(1.05);
      color: red;
    }

    &_title {
      margin-top: 24px;
      font-size: 20px;
      font-weight: 600;
      line-height: 25.56px;
      color: #000;
    }
  }
}

.phones {
  background: #ffe4cb;
  background-image: url(../../../../public/img/category-phones.png);
  background-position: +70px +10px;
  background-size: cover;
  background-repeat: no-repeat;
}

.tablets {
  background: #85868b;
  background-image: url(../../../../public/img/category-tablets.png);
  background-position: +20px +20px;
  background-size: 150%;
  background-repeat: no-repeat;
}

.accessories {
  background: #b03d62;
  background-image: url(../../../../public//img/category-accessories.png);
  background-position: +30px +50px;
  background-size: 190%;
  background-repeat: no-repeat;
}
