@import '../utils/mixins.scss';

.title {
  text-align: center;
}

.cart {
  font-size: 32px;
  font-weight: 700;
  line-height: 40.9px;
  letter-spacing: -0.01em;
  display: flex;
  margin: auto;
  margin-bottom: 24px;
}

.img {
  text-align: center;
  margin: auto;
}

.cartList {
  display: flex;
  flex-wrap: wrap;
  max-width: 1136px;
  justify-content: center;
  grid-gap: 16px;
  justify-items: center;
  margin: auto;

  &__link {
    text-decoration: none;
    color: #313237;
    display: flex;
    align-items: center;
  }

  &__carts {
    width: 752px;
  }

  &__cart {
    display: flex;
    align-items: center;
    height: 128px;
    border: 1px solid #b4bdc3;
    margin-block-end: 16px;

    .container_img {
      width: 80px;
      height: 80px;
    }

    &_img {
      max-width: 80px;
      max-height: 80px;
      margin-inline-start: 24px;
    }

    &_delete {
      border: none;
      background-color: white;
      color: #b4bdc3;
      margin-inline-start: 24px;
      cursor: pointer;

      @include buttonTransitionText;
    }

    &_title {
      font-size: 14px;
      font-weight: 500;
      line-height: 21px;
      margin-inline-start: 24px;
      width: 295px;

      @include buttonTransitionText;

      @media (max-width: 710px) {
        max-width: 150px;
      }

      @media (max-width: 580px) {
        max-width: 100px;
      }
    }

    &_increase,
    &_reduction {
      width: 32px;
      height: 32px;
      background-color: #fff;
      cursor: pointer;
      border: 2px solid #89939a;

      @include buttonTransitionWhite;
    }

    &_reduction {
      margin-inline-start: 49px;

      &_isNotActive {
        cursor: default;
        border: 2px solid #d6d5d5;
        &:hover {
          box-shadow: none;
          border: 2px solid #d6d5d5;
        }
      }
    }

    &_number {
      width: 32px;
      height: 32px;
      display: flex;
      text-align: center;
      align-items: center;
      justify-content: center;
    }

    &_price {
      margin-inline-start: 43px;
      font-size: 22px;
      font-weight: 700;
      line-height: 30.8px;
      padding-right: 15px;
    }
  }

  &__price {
    width: 368px;
    height: 206px;
    text-align: center;
    border: 1px solid #b4bdc3;
    box-sizing: border-box;

    @media (max-width: 1095px) {
      width: 752px;
      margin-bottom: 40px;
    }

    &_title {
      font-size: 32px;
      font-weight: 700;
      line-height: 40.9px;
      letter-spacing: -0.01em;
      text-align: center;
      margin-top: 24px;
    }

    &_about {
      display: flex;
      text-align: center;
      justify-content: center;
      align-items: center;
      margin: auto;
      padding-bottom: 24px;
      border-bottom: 1px solid #b4bdc3;
      color: #b4bdc3;
      width: 321px;
      margin-bottom: 24px;
    }

    &_button {
      color: #fff;
      display: flex;
      justify-content: center;
      margin: auto;
      align-items: center;
      background-color: #313237;
      width: 320px;
      height: 48px;
      cursor: pointer;
      text-decoration: none;

      @include buttonTransitionBlack;

      &:active {
        color: #313237;
        background-color: #fff;
      }
    }
  }
}
